body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  overflow: hidden;
}

.App {
  max-height: 100vh;
  padding: 2vh 0;
}

a {
  cursor: pointer;
}

h1 {
  margin: 5px 0;
  font-size: 4em;
}

h2 {
  font-size: 3em;
}

h3 {
  font-size: 2em;
}

h4 {
  font-size: 1em;
}

h5 {
  font-size: 1em;
  margin: 0;
  color: grey;
  text-transform: uppercase;
}

.Header {
  margin: 4vh 3vw;
  position: relative;
}

.Header p {
  position: absolute;
  right: 0;
  top: 0;
  width: 30vw;
  margin: 0;
  font-size: 0.8em;
  line-height: 2em;
  color: #666;
  text-align: right;
  text-align: justify;
  text-justify: inter-word;
}

.Header p > b {
  display: block;
  color: #333;
  font-size: 1.5em;
  margin-bottom: 10px;
}

main {
  -webkit-transition: 0.8s all ease;
  transition: 0.8s all ease;
}

main.sidebar-closed {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

main.sidebar-open {
  -webkit-transform: translateX(-30%);
          transform: translateX(-30%);
}

.map-container {
  width: 60%;
  margin: 0 auto;
  position: relative;
}

.Map img {
  width: 100%;
}

.Pin {
  position: absolute;
  cursor: pointer;
}

.Pin * {
  display: inline-block;
  vertical-align: bottom;
}

.Pin img {
  width: 30px;
}

.Pin h4 {
  color: #333;
  font-size: 20px;
  margin: 0 5px;
  line-height: 30px;
}

.sidebar {
  visibility: hidden;
  position: absolute;
  width: 30vw;
  top: 0;
  right: -30vw;
  height: 100vh;
  background: white;
  border-left: 1px solid grey;
  padding: 3vh 0;
  -webkit-transition: all .8s ease;
  transition: all .8s ease;
}

.sidebar.open {
  visibility: visible;
  right: 0;
  padding: 3vh 3vw;
}

.sidebar.open .sidebar-close-button {
  color: grey;
  position: absolute;
  top: 3vh;
  right: 3vw;
  font-size: 2em;
}

.sidebar span {
  display: inline-block;
  background: blue;
  border-radius: 50%;
  padding: 5px;
  width: 30px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  vertical-align: middle;
  color: white;
  margin-right: 15px;
}

.current-mode {
  position: absolute;
  bottom: 2vh;
  left: 0;
  padding: 3vh 3vw;
}

.mode-selector {
  display: inline-block;
  padding-bottom: 3px;
  border-bottom: 2px solid;
  margin: 0 5px;
  text-align: center;
  box-sizing: content-box;
  background: white;
}

.mode-options:after {
  content: '\25BC';
  top: -5px;
}

.mode-options a {
  display: block;
  border: 1px solid;
  padding: 2px 4px;
  margin: 0;
}

.mode-options.showing {
  visibility: visible;
}

.mode-options.hidden {
  visibility: hidden;
}
